import PressIconPlus from '../press-icon-plus/press-icon-plus.vue';
import { GREEN, GRAY_DARK } from '../common/constant/color';
import { defaultProps, defaultOptions } from '../common/component-handler/press-component';
import utils from '../common/utils/utils';
import status from './index-status';

export default {
  name: 'PressSteps',
  options: {
    ...defaultOptions,
    styleIsolation: 'shared',
  },
  components: {
    PressIconPlus,
  },
  props: {
    icon: { type: String, default: '' },
    steps: { type: Array, default: () => [] },
    active: { type: Number, default: -1 },
    direction: {
      type: String,
      default: 'horizontal',
    },
    activeColor: {
      type: String,
      default: GREEN,
    },
    inactiveColor: {
      type: String,
      default: GRAY_DARK,
    },
    activeIcon: {
      type: String,
      default: 'checked',
    },
    inactiveIcon: { type: String, default: '' },
    descClass: {
      type: String,
      default: '',
    },
    ...defaultProps,
  },
  emits: ['click-step'],
  data() {
    return {
      status,
    };
  },
  computed: {
    stepsClass() {
      const { direction, customClass } = this;
      return `${customClass} ${utils.bem2('steps', [direction])}`;
    },
  },
  methods: {
    onClick(event) {
      const { index } = event.currentTarget.dataset;
      this.$emit('click-step', index);
    },
    getStepClass(index) {
      const { direction, active } = this;
      return `${utils.bem2('step', [direction, status(index, active)])} press-hairline`;
    },
    getStepStyle(index) {
      const { active, inactiveColor } = this;
      return status(index, active) === 'inactive' ? `color: ${inactiveColor}` : '';
    },
  },
};

